@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

$card-width-desctop: 272px;
$card-width-tablet-mobile: 288px;
$column-gap: 16px;

.catalog {
  margin: 0 auto;
  display: grid;
  max-width: 288px;
  grid-template-columns: 1fr;
  column-gap: $column-gap;
  row-gap: 40px;

  @include onTablet {
    max-width: $card-width-tablet-mobile * 2 + $column-gap;
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: 900px) {
    max-width: $card-width-tablet-mobile * 3 + $column-gap * 2;
    grid-template-columns: 1fr 1fr 1fr;
  }

  @include onDesktop {
    max-width: $card-width-tablet-mobile * 4 + $column-gap * 3;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
